export default{
  data () {
    return {
      opt: {},              // 传递过来的商品参数
      address: {},          // 收货地址
      good: {
        name: '电子烟电子烟',
        norm: '黑色普通版',
        integral: 999,
        num: 1,
        fare: 100
      },
      goods_list: [],            // 商品列表
      order_pay_price: 0,        // 订单需支付金额
      order_total_discount: 0,   // 订单折扣优惠总额
      order_total_num: 0,        // 订单总数
      order_total_price: 0,      // 订单总金额
      express_price: 0,          // 运费
      buyStatus: false,          // 是否点击确认购买
    }
  },
  onLoad(opt) {
    let obj= JSON.parse(opt.obj)
    this.opt= obj
    
    this.getDetail()
  },
  methods: {
    
    // 获取详情信息
    getDetail() {
      let that= this,
        obj= this.opt
      obj.delivery= 10
      let params = {
        url: that.$api.buyNow,
        data: obj
      }
      that.$httpRequest(params).then(res => {
        that.address= res.data.address
        that.goods_list= res.data.goods_list
        that.order_pay_price= res.data.order_pay_price
        that.order_total_discount= res.data.order_total_discount
        that.order_total_num= res.data.order_total_num
        that.order_total_price= res.data.order_total_price
        that.express_price= res.data.express_price
      })
    },
    
    // 确认购兑换
    goPuy() {
      uni.showLoading({
        title: '正在提交订单'
      })
      let that= this,
        obj= this.opt
      if(!this.buyStatus) {
        this.buyStatus= true
        obj.delivery= 10
        let params = {
          url: that.$api.buyNow,
          data: obj,
          method: 'POST'
        }
        that.$httpRequest(params).then(res => {
          uni.hideLoading()
          if(res.code === 1) {
            uni.navigateTo({
              url: 'success?num=' + that.order_pay_price + '&order_id=' + res.data.order_id
            })
          } else {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          }
        })
        setTimeout(function() {
          that.buyStatus= false
        }.bind(that), 1000)
      } else {
        uni.showToast({
          title: '请稍后再试',
          icon: 'none'
        })
      }
    },
    // 返回
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    }
  }
}
